import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Match from '../views/Match.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
  	path: '/match/:id',
  	name: 'Match',
  	component: Match
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
